import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { Router } from '@angular/router'
import { faQuestionCircle } from '@fortawesome/pro-light-svg-icons'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { pick } from 'lodash'
import { combineLatest, forkJoin, Observable, of, Subject, Subscription, throwError } from 'rxjs'
import { mergeMap, map, switchMap, tap, catchError } from 'rxjs/operators'
import { ICurrency, IFee, IUser, Transaction, TransactionMethod, Wallet } from 'src/app/common/api-interfaces'
import { BeneficiaryDetailsFormComponent } from 'src/app/common/beneficiary-form/beneficiary-details-form/beneficiary-details-form.component'
import { Beneficiary } from 'src/app/common/models/accounting/beneficiary.model'
import { Currency } from 'src/app/common/models/accounting/currency.model'
import { openNoAccessModal } from 'src/app/common/no-access-modal/no-access-modal.component'
import { BeneficiaryBookComponent } from 'src/app/common/payment/beneficiary-book/beneficiary-book.component'
import { PaymentConfirmationComponent } from 'src/app/common/payment/payment-confirmation/payment-confirmation.component'
import { FeeService } from 'src/app/common/services/accounting/fee.service'
import { SessionService } from 'src/app/common/services/session.service'
import { TransactionViewComponent } from 'src/app/common/transaction/transaction-view/transaction-view.component'
import { CurrenciesQuery } from 'src/app/store/currencies/currencies.query'
import { WalletsQuery } from 'src/app/store/wallets/wallets.query'
import { WalletsService } from 'src/app/store/wallets/wallets.service'
import { environment } from 'src/environments/environment'

@Component({
    selector: 'withdraw-form',
    templateUrl: 'withdraw-form.component.html',
})
export class WithdrawFormComponent implements OnInit, OnDestroy, OnChanges {
    @Input()
    public transaction: Partial<Transaction>
    public files: File[] = []
    public minAmount: string | number | undefined
    public isUploadCollapsed = true
    public saveAsTemplate = true
    public inclusiveFee = true

    public faQuestionCircle = faQuestionCircle
    public appName = environment.appName

    public purposeItems = [
        'Employment Income',
        'Business Expenses',
        'Tax Payment',
        'License/Subscription Fee',
        'Bill/Utilities Payment',
        'Insurance Payment',
        'Commission/Bonus Payment',
        'Family Maintenance (Education, Medical, Travel)',
        'Investment/Securities',
        'Transport',
        'Property Payment',
    ]

    @ViewChild('withdrawForm')
    public withdrawForm: NgForm

    @ViewChild('beneficiaryDetailsForm')
    public beneficiaryDetailsForm: BeneficiaryDetailsFormComponent

    private fee: IFee | null = null

    private subscriptions = new Subscription()
    private fetchEvent = new Subject<void>()

    private beneficiary: Beneficiary | undefined
    constructor(
        public session: SessionService,
        private ngbModal: NgbModal,
        private http: HttpClient,
        private feeService: FeeService,
        private walletsService: WalletsService,
        private walletsQuery: WalletsQuery,
        private router: Router,
        private currenciesQuery: CurrenciesQuery,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            combineLatest([this.fetchEvent, this.session.userStream])
                .pipe(
                    tap(([_, user]) => {
                        this.transaction.baseWallet = {
                            user: pick(user, ['id', 'accountNumber', 'name']),
                        } as Wallet
                    }),
                    switchMap(([_, user]) =>
                        this.feeService.fetch({
                            type: 'withdrawal',
                            method: this.transaction.method as TransactionMethod,
                            user: { id: user.id } as IUser,
                            currency: { code: this.transaction.currency?.code } as ICurrency,
                        })
                    )
                )
                .subscribe(fee => {
                    this.fee = fee
                    Object.assign(this.transaction, this.feeService.determineFee(this.transaction, fee as any))
                    if (fee && fee.minValue) {
                        this.minAmount = Currency.convert(fee.minValue, fee.currency, this.transaction.currency!)
                    }
                })
        )
    }

    public ngOnChanges(changes: SimpleChanges): void {
        if (changes.transaction) {
            this.reset()
        }
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public submit(): void {
        if (this.beneficiaryDetailsForm) {
            this.beneficiaryDetailsForm.submit()
            if (!(this.beneficiaryDetailsForm.isValid() || this.beneficiaryDetailsForm.disabled)) {
                return
            }
        }
        if (this.withdrawForm && this.withdrawForm.invalid) {
            return
        }
        if (this.session.user.accessErrors.length > 0) {
            openNoAccessModal(this.ngbModal, this.session.user.accessErrors[0])
            return
        }

        const modal = this.ngbModal.open(PaymentConfirmationComponent, {
            windowClass: 'modal-primary',
            backdrop: 'static',
        })
        const component = modal.componentInstance as PaymentConfirmationComponent
        component.transaction = this.transaction as Transaction
        component.onConfirm.subscribe(() => {
            this.createTransaction()
                .pipe(
                    catchError(err => {
                        component.isSubmitted = false
                        component.reset()
                        return throwError(err)
                    })
                )
                .subscribe(transaction => {
                    modal.close()
                    const transactionView = this.ngbModal.open(TransactionViewComponent, {
                        windowClass: 'modal-primary',
                        backdrop: 'static',
                    })
                    const transactionViewModal = transactionView.componentInstance as TransactionViewComponent
                    transactionViewModal.transaction = transaction
                    this.reset()
                    this.walletsService.fetch()
                    this.fetchEvent.next()
                })
        })
    }

    public createTransaction(): Observable<Transaction> {
        if (this.saveAsTemplate && this.transaction.beneficiary && !this.transaction.beneficiary.id) {
            const wallet = this.transaction.baseWallet
            if (wallet) {
                this.transaction.beneficiary.user = wallet.user
            }
        }
        const body = {
            ...this.transaction,
        }
        if (!this.inclusiveFee) {
            body.amount = undefined
            body.settledAmount = this.transaction.amount
        }
        return this.http.post<Transaction>('/transactions', body).pipe(
            mergeMap(transaction =>
                this.files.length > 0
                    ? forkJoin(
                          this.files.map(file =>
                              this.http.post<File>(`/transactions/${transaction.id}/files`, file, {
                                  headers: new HttpHeaders({
                                      'X-File-Name': encodeURIComponent(file.name),
                                      'Content-Type': file.type,
                                  }),
                              })
                          )
                      ).pipe(map(() => transaction))
                    : of(transaction)
            )
        )
    }

    public openBeneficiaryBook(): void {
        const modal = this.ngbModal.open(BeneficiaryBookComponent, {
            windowClass: 'modal-primary',
            size: 'lg',
        })
        ;(modal.componentInstance as BeneficiaryBookComponent).onSelect.subscribe(async (beneficiary: Beneficiary) => {
            if (beneficiary.method === this.transaction.method) {
                this.transaction.beneficiary = beneficiary
                this.saveAsTemplate = false
            } else {
                this.beneficiary = beneficiary
                this.router.navigate(['/withdraw'], {
                    queryParams: { method: beneficiary.method !== 'swift' ? beneficiary.method : null },
                })
            }
            modal.close()
        })
    }

    public useMaxAmount(): void {
        if (this.transaction.currency) {
            this.walletsQuery.balances$.subscribe(balances => {
                const found = balances.find(balance => balance.currency.code === this.transaction.currency!.code)
                if (found) {
                    this.transaction.amount = found.availableBalance

                    if (!this.inclusiveFee && this.fee) {
                        const maxFee = new BigNumber(
                            Currency.convert(this.fee.max!, this.fee.currency, this.transaction.currency!)
                        )
                        const relativeFeeAmount = new BigNumber(this.transaction.amount).times(
                            this.transaction.relativeFee ?? 0
                        )
                        let fee = new BigNumber(this.transaction.fixedFee || 0).plus(relativeFeeAmount)
                        if (fee.isGreaterThan(maxFee)) {
                            fee = maxFee
                        }
                        this.transaction.amount = new BigNumber(this.transaction.amount)
                            .minus(fee)
                            .toFixed(this.transaction.currency!.decimalPlaces, 1)
                    }

                    if (new BigNumber(this.transaction.amount).isLessThan(0)) {
                        this.transaction.amount = '0'
                    }

                    this.transaction.amount = parseFloat(this.transaction.amount).toString()
                }
            })
        }
    }

    public toggleInclusiveFee(): void {
        this.inclusiveFee = !this.inclusiveFee
        this.transaction.settledAmount = !this.inclusiveFee ? this.transaction.amount : undefined
    }

    public onCurrencyChange(): void {
        this.fetchEvent.next()
    }

    public onAmountChange(): void {
        Object.assign(this.transaction, this.feeService.determineFee(this.transaction, this.fee as any))
    }

    public reset(): void {
        this.saveAsTemplate = true
        this.inclusiveFee = true
        this.minAmount = undefined
        this.files = []
        if (this.withdrawForm) {
            this.withdrawForm.resetForm({
                saveAsTemplate: true,
                inclusiveFee: true,
                amount: undefined,
                purpose: undefined,
                comment: undefined,
                'currency-select': this.transaction.currency,
            })
        }
        if (this.beneficiaryDetailsForm) {
            this.beneficiaryDetailsForm.reset()
        }
        if (!this.beneficiary) {
            this.transaction.beneficiary = pick(this.transaction.beneficiary, ['currency', 'method']) as any
        } else {
            this.transaction.beneficiary = this.beneficiary
            if (this.beneficiary.method === 'local') {
                this.transaction.currency = this.currenciesQuery.getEntity(this.beneficiary.currency.code)
            }
            this.beneficiary = undefined
            this.saveAsTemplate = false
        }
    }
}
