import { Component, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute } from '@angular/router'
import { Subscription } from 'rxjs'
import { IBeneficiary, Transaction, TransactionMethod, Wallet } from '../common/api-interfaces'
import { SessionService } from '../common/services/session.service'

@Component({
    selector: 'withdraw',
    templateUrl: 'withdraw.component.html',
})
export class WithdrawComponent implements OnInit, OnDestroy {
    public transaction: Partial<Transaction>
    private subscriptions = new Subscription()

    constructor(private route: ActivatedRoute, public session: SessionService) {}

    public async ngOnInit(): Promise<void> {
        if (this.route.snapshot.queryParamMap.has('method')) {
            this.resetTransaction(this.route.snapshot.queryParamMap.get('method') as TransactionMethod)
        }
        this.route.queryParamMap.subscribe(params => {
            this.resetTransaction((params.get('method') || 'swift') as TransactionMethod)
        })
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    private resetTransaction(method: TransactionMethod): void {
        this.transaction = {
            type: 'withdrawal',
            method,
            counterWallet: {
                user: null,
            } as Wallet,
            beneficiary: {
                method: method as any,
            } as IBeneficiary,
        }
    }
}
